<template>
	<div style="margin-top:-30px;margin-left: -25px;">
		<!-- <BreadList :list="['首页']" /> -->
		<a-card>
			<baidu-map class="map" :center="{lng: 111.852917, lat: 32.660714}" :zoom="17">
				<bm-marker  :key="item.index" v-for="item,index in mapInfo" :position="{lng: item.lng, lat: item.lat}" :dragging="true"
				@click="infoWindowOpen(index)">
					<bm-info-window :show="item.show" @close="infoWindowClose" @open="infoWindowOpen(index)">
						事故数量:{{item.counts}} <a>查看</a>{{item.lng}}
					</bm-info-window>
				</bm-marker>
			</baidu-map>
		</a-card>
	</div>
</template>

<script>
	import {
		getMapInfo
	} from "@/api/Fix.js"
	export default {
		data() {
			return {
				mapInfo: []
			}
		},
		created() {
			this.initMapInfo()
		},
		methods: {
			async initMapInfo() {
				const {
					data: res
				} = await getMapInfo();

				if (res.code !== 20005) return this.$message.error("数据加载出错");
				this.mapInfo = res.data;
				this.mapInfo.forEach(item => {
					if(item.coordinates!=null){
						this.$set(item, 'lng', item.coordinates.slice(1, -1).split(',')[0])
						this.$set(item, 'lat', item.coordinates.slice(1, -1).split(',')[1])
						this.$set(item, 'show', false)
					}
				})
				console.log(this.mapInfo)
			},
			infoWindowClose(index) {
				this.mapInfo[index].show = false
			},
			infoWindowOpen(index) {
				this.mapInfo[index].show = true
			}
		}

	}
</script>

<style>
	.map {
		width: 100%;
		height: 900px;
	}
</style>
